<template lang="pug">
  v-card.my-profile-address(tile)
    v-toolbar(card dark color="primary")
      v-btn(icon @click.native="isActive = false" dark)
        v-icon close
      v-toolbar-title Room

    v-card-text(style="position: relative; max-width: 500px; margin: auto;")
      loading(:loading="loading" :oops="oops")
      v-layout(row wrap)
        v-flex(d-flex xs12 sm12 md6)
          v-card
            v-list
              v-list-tile(@click="openDialogFull('EnterRoom')")
                v-list-tile-action
                  v-icon exit_to_app
                v-list-tile-content
                  v-list-tile-title Enter
              v-list-tile {{ data.message }}

</template>

<script>
import api from '@/auth/helpers'
import auth from '@/auth/index'
import store from '../store'

export default {
  name: 'EnterRoom',

  props: {
    active: {
      type: Boolean,
      default: false
    }
  },

  data () {
    return {
      data: {
        name: null,
        nickname: null,
        gender: null,
        email: null,
        country: null,
        intro: null,
        avatar: null,
        bg_music: null,
        bg_image: null,
        follow_num: 0,
        follower_num: 0,
        message_num: 0,
        liked_num: 0,
        message: null
      },
      loading: false,
      oops: false
    }
  },

  computed: {
    isActive: {
      get () {
        return this.active
      },
      set (val) {
        this.$emit('update:active', val)
      }
    }
  },

  mounted () {
    this.enterRoom()
  },

  methods: {
    enterRoom () {
      var form = {
        id: store.state.porch.id
      }
      var data = {
        instruction: JSON.stringify(form)
      }
      const header = ['enter-room', null]
      if (!auth.isValidLogin()) {
        auth.expiredLogin()
      } else {
        api.fullRequest(api.infoConfig(JSON.stringify(data), header))
          .then(res => {
            this.data = res.data.result
          })
          .catch((error) => {
            console.log(error)
          })
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
.my-profile-address
  height: 100%
</style>



