<template lang="pug">
v-container.my-porch(
  fluid
  fill-height
  text-xs-center
  v-bind:grid-list-sm="$vuetify.breakpoint.smAndDown"
  v-bind:grid-list-lg="$vuetify.breakpoint.mdAndUp"
)
  v-layout(row wrap)
    v-flex(d-flex xs12 sm12 md6)
      v-card
        v-list
          v-list-tile(@click="openDialogFull('EnterRoom', 0)")
            v-list-tile-action
              v-icon exit_to_app
            v-list-tile-content
              v-list-tile-title Enter
          v-list-tile {{ data.members[0] }}
    v-flex(d-flex xs12 sm12 md6)
      v-card
        v-list
          v-list-tile(@click="openDialogFull('EnterRoom', 1)")
            v-list-tile-action
              v-icon exit_to_app
            v-list-tile-content
              v-list-tile-title Enter
          v-list-tile {{ data.members[1] }}
    v-flex(d-flex xs12 sm12 md6)
      v-card
        v-list
          v-list-tile(@click="openDialogFull('EnterRoom', 2)")
            v-list-tile-action
              v-icon exit_to_app
            v-list-tile-content
              v-list-tile-title Enter
          v-list-tile {{ data.members[2] }}
    v-flex(d-flex xs12 sm12 md6)
      v-card
        v-list
          v-list-tile(@click="openDialogFull('EnterRoom', 3)")
            v-list-tile-action
              v-icon exit_to_app
            v-list-tile-content
              v-list-tile-title Enter
          v-list-tile {{ data.members[3] }}
    v-flex(d-flex xs12 sm12 md6)
      v-card
        v-list
          v-list-tile(@click="openDialogFull('EnterRoom', 4)")
            v-list-tile-action
              v-icon exit_to_app
            v-list-tile-content
              v-list-tile-title Enter
          v-list-tile {{ data.members[4] }}
    v-flex(d-flex xs12 sm12 md6)
      v-card
        v-list
          v-list-tile(@click="openDialogFull('EnterRoom', 5)")
            v-list-tile-action
              v-icon exit_to_app
            v-list-tile-content
              v-list-tile-title Enter
          v-list-tile {{ data.members[5] }}

    v-dialog(
      v-model="dialogFullActive"
      fullscreen
      transition="dialog-bottom-transition"
      :overlay=false
      scrollable
    )
      component(:is="dialogFullComp" :active.sync="dialogFullActive")
</template>

<script>
import api from '@/auth/helpers'
import auth from '@/auth/index'
import store from './store'
import EnterRoom from './components/enter-room'

export default {
  name: 'Porch',

  components: {
  },

  data () {
    return {
      data: {
        game_floor: null,
        members: [{id: null, name: null, avatar: null, isempty: true},
          {id: null, name: null, avatar: null, isempty: true},
          {id: null, name: null, avatar: null, isempty: true},
          {id: null, name: null, avatar: null, isempty: true},
          {id: null, name: null, avatar: null, isempty: true},
          {id: null, name: null, avatar: null, isempty: true}]
      },
      dialogFullActive: false,
      dialogFullComp: null,
      test: store.state.porch.test
    }
  },

  mounted () {
    this.enterIndex()
  },

  methods: {

    enterIndex () {
      var form = {}
      const header = ['enter-index', null]
      if (!auth.isValidLogin()) {
        auth.expiredLogin()
      } else {
        api.fullRequest(api.infoConfig(JSON.stringify(form), header))
          .then(res => {
            this.data = res.data.result
          })
          .catch((error) => {
            console.log(error)
          })
      }
    },

    openDialogFull (comp, num) {
      if (comp === 'EnterRoom') this.dialogFullComp = EnterRoom
      this.updateOwner(this.data.members[num].id)
      if (store.state.porch.id) {
        this.dialogFullActive = true
      }
    },

    updateOwner (id) {
      store.dispatch('porch/updateOwner', id)
    }
  }
}
</script>

<style lang="stylus">
.my-porch

  &__media
    height: 100%
    margin: 0

  .picker__title
    display: none !important

</style>



